import React, { useState, useEffect } from 'react';
import imagenLogin from '../assets/checkmark.png';
import appFirebase from './credenciales';
import { getAuth, createUserWithEmailAndPassword, signInWithEmailAndPassword, GoogleAuthProvider, signInWithPopup } from 'firebase/auth';

const auth = getAuth(appFirebase);

const Login = () => {
  const [registrando, setRegistrando] = useState(false);
  const [mensaje, setMensaje] = useState('');

  const functauth = async (e) => {
    e.preventDefault();
    const correo = e.target.email.value;
    const contrasea = e.target.password.value;

    try {
      if (registrando) {
        await createUserWithEmailAndPassword(auth, correo, contrasea);
        setMensaje('Usuario registrado con xito!');
        // Limpia los campos del formulario despus de un registro exitoso
        e.target.email.value = '';
        e.target.password.value = '';
      } else {
        await signInWithEmailAndPassword(auth, correo, contrasea);
        setMensaje('Inicio de sesin exitoso!');
        // Aqu podras redirigir a la pgina de inicio o hacer alguna accin despus de iniciar sesin
      }
    } catch (error) {
      console.error('Error de autenticacin:', error);
      if (registrando && error.code === 'auth/email-already-in-use') {
        setMensaje('El correo electrnico ya est en uso. Intenta iniciar sesin.');
      } else {
        setMensaje('Error al autenticar. Verifica tus datos e intenta nuevamente.');
      }
    }
  };

  const signInWithGoogle = async () => {
    const provider = new GoogleAuthProvider();
    try {
      const result = await signInWithPopup(auth, provider);
      const user = result.user;
      setMensaje('Inicio de sesin con Google exitoso!');
      // Aqu podras redirigir a la pgina de inicio o hacer alguna accin despus de iniciar sesin
    } catch (error) {
      console.error('Error al iniciar sesin con Google:', error);
      setMensaje('Error al iniciar sesin con Google. Intenta nuevamente.');
    }
  };

  useEffect(() => {
    setMensaje('');
  }, [registrando]);

  return (
    <div className="container">
      <div className="row">
        <div className="col-md-4">
          <div className="padre">
            <div className="card card-body shadow-lg">
              <img src={imagenLogin} alt="" className='imagen-login'/>
              <form onSubmit={functauth}>
                <input type="text" placeholder='Correo electrnico' className='caja-texto' id='email' />
                <input type="password" placeholder='Contrasea' className='caja-texto' id='password' />
                <button className='btnform'>{registrando ? "Registrarse" : "Iniciar sesin"}</button>
              </form>
              <button className='btnform' onClick={signInWithGoogle}>Iniciar sesin con Google</button>
              <h4 className='textbtnform2'>{registrando ? "Ya tienes cuenta?" : "No tienes cuenta?"}<button className='btnform2' onClick={() => setRegistrando(!registrando)}>{registrando ? "Iniciar sesin" : "Registrarse"}</button></h4>
              {mensaje && <p className="mensaje">{mensaje}</p>}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;
